<template>
  <div class='about marTop_50'>
    <div class='container'>
      <div class='row'>
        <div class='col-xs-12 col-sm-7'>
          <div class="panel panel-default">
              <div class="panel-body">
                  <h2 v-text='userInfo.name'></h2>
                  <p class="lead" v-text='userInfo.content'></p>
                  <h4 class="text-info">技能介绍</h4>
                  <p v-for='(item,index) in userInfo.skill' class="lead" v-text='item'></p>
              </div>
          </div>
        </div>     
        <div class='col-xs-12 col-sm-5'>
          <img class='img_me img-thumbnail' src="../assets/img/me.jpg" alt='照片'/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  data () {
      return {
        msg: 'about Vue',
        name: 'Reborn',
        userInfo:{},
      }
    },
  components: {

  },
  mounted() {
    this.loadInfo();
  },
  methods:{
    foo() {
      console.log(jQuery.fn.jquery)
    },
    loadInfo(){
      this.$http.get('/static/user.json').then(function(response){
          // 响应成功回调
          var data = response.body;
          if (data) {
            this.userInfo = data;
          }else{
            console.warn('data is null')
          };
      }, function(response){
          // 响应错误回调
          console.log('error');
      });
    }
  }
}
</script>

<style scoped>
.lead{
  line-height: 2;
}
.img_me{
  width: 100%;
  max-width: 300px;
}
</style>



